import {React,PageView,observer,PageContainer,Row,CheckBox,Form,Input,Select,Button,CheckGroup} from "bricks-web"
const {FormItem,FormRepeat} = Form;

@PageView()
class FormDemo extends React.PureComponent {

  componentDidMount() {
  }

  constructor(props){
    super(props);
  }

  add(formProps){
    formProps.form.store.Lists.push({itemselectValue:"3"});
  }
  showData(formProps){
    alert(JSON.stringify(formProps.form.store));
  }
  Reset(formProps){
    formProps.form.reset();
  }

  createRow(rowProps){
    return (
      <div>
          <FormItem {...rowProps} dataKey='name' label={""} com={Input}/>
          <FormItem label=':' {...rowProps} dataKey='address' com={Input}/>
          <FormItem label=":" {...rowProps} dataKey='isChecked' initialValue={false} com={CheckBox}>
            
          </FormItem>
          <FormItem 
          initialValue='2'
          label=':'
          rule={[
            {message:'!',regex:'required'},
            {message:'10',regex:(val)=>{
              if(val>10){
                return false;
              }
              return true;
            }}
          ]} {...rowProps} dataKey='itemselectValue' data={[{label:"",value:'1'},{label:"",value:'2'},{label:"",value:'3'}]} placeholder='' com={Select}  />
      </div>
    )
  }

  render() {
    return <div className='doc-page-content'>
          <Form initialValue={{
            inputValue:'hahha'
          }} renderContent={(formProps)=>{
              return (
                <React.Fragment>
                   <Row gutter={16}>
                    <Row.Col span={{ md: 12, lg: 8 }}>
                      <FormItem label='' {...formProps} rule={[]} dataKey='inputValue' placeholder='' com={Input}  />
                    </Row.Col>
                    <Row.Col span={{md: 12, lg: 8 }}>
                     <FormItem label='' {...formProps} rule={[]} initialValue='200' dataKey='inputValue1' com={Input}  />
                    </Row.Col>
                    <Row.Col span={{md: 12, lg: 8 }}>
                     <FormItem label=':' {...formProps} rule={[]} initialValue='112' dataKey='inputdate' com={Input}  />
                    </Row.Col>
                    <Row.Col span={{ md: 12, lg: 8}}>
                        <FormItem label=':' {...formProps} rule={[]} initialValue={[0]} data={[
                        {label:'check1',value:0},
                        {label:'check2',value:1},
                        {label:'check3',value:2},
                        {label:'check4',value:3},
                        {label:'check5',value:4},
                        {label:'check6',value:5},
                        {label:'check7',value:6},
                        {label:'check8',value:7}
                      ]} dataKey='checkgroup' com={CheckGroup}  />
                    </Row.Col>
                  </Row>
                
                  <FormItem 
                    initialValue='2'
                    rule={[
                      {message:'!',regex:'required'},
                      {message:'10',regex:(val)=>{
                        if(val>10){
                          return false;
                        }
                        return true;
                      }}
                    ]}
                    {...formProps} 
                    label=':'
                    dataKey='selectorValue' data={[{label:"",value:'1'},{label:"",value:'2'},{label:"",value:'3'}]} placeholder='' com={Select}  />
                  <FormRepeat initialValue={[{name:"hxz",isChecked:true}]} {...formProps} dataKey='Lists' renderRow={(rowProps)=>{
                    return this.createRow(rowProps);
                  }} />
                  <Button type='primary' onClick={this.add.bind(this,formProps)}></Button>
                  <Button type='primary' onClick={this.showData.bind(this,formProps)}>ShowData</Button>
                  <Button type='primary' onClick={this.Reset.bind(this,formProps)}>Reset</Button>
                </React.Fragment>);
            }}/>
    </div>
  }
}

export default FormDemo;