import React from 'react';
import { Tabs } from 'bricks-web';
import './index.less';

export default class Example extends React.Component{
    constructor(props){
        super(props);
        this.state={
            data:[ {label:"",key:'home',allowClose:true},
            {label:"",key:'email',allowClose:true},
            {label:"",key:'info',allowClose:true},
            {label:"",key:'Inbox',allowClose:true},
            {label:"",key:'other',allowClose:true},
            {label:"",key:'message',allowClose:true},
            {label:"1",key:'message1',allowClose:true},
            {label:"2",key:'message2',allowClose:true},]
        }
    }
    renderItem = (key)=>{
        return <div>{key}</div>
    }
    render(){
        return <div>
            <Tabs style={{width:320}} 
                renderItem={this.renderItem.bind(this)} 
                defaultSelectedKey='home'
                data={this.state.data} 
            />
            <br />
            <Tabs 
                tabPosition='left'
                style={{height:200}} 
                renderItem={this.renderItem.bind(this)} 
                defaultSelectedKey='home'
                data={this.state.data} 
            />
        </div>;
    }
}