import React from 'react';
import { Tabs } from 'bricks-web';
import './index.less';

export default class Example extends React.Component{
    renderItem = (key)=>{
        return <div>{key}</div>
    }
    render(){
        return <div>
            <Tabs 
                defaultSelectedKey='home'
                //  height:100% 
                style={{height:200}}
                tabPosition='bottom' 
                renderItem={this.renderItem.bind(this)} 
                data={[
                    {label:"",key:'home'},
                    {label:"",key:'email'},
                    {label:"",key:'info'},
                ]} />
        </div>;
    }
}